import Vue from 'vue'
import Nav from './Nav/index.js' // 
import publicFoot from './publicFoot/index.js' // 
import banner from './banner/index.js' // 
import contentNav from './contentNav/index.js' // 
import rightFixed from './rightFixed/index.js' // 

const components = [
    Nav,
    publicFoot,
    banner,
    contentNav,
    rightFixed
];

components.map(component => {
    Vue.component(component.name, component)
});



