import Vue from 'vue'
import Router from 'vue-router'

/**
 * push
 */
const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error => error)
}
Vue.use(Router)

export default new Router({
    routes: [
        //
        {
            path: '/',
            name: 'index',
            component: require('../newPage/index.vue').default,
            meta:{
                nav:'/',
            }
        },
        {
            path: '/index',
            name: 'index',
            component: require('../pages/index.vue').default,
        }, {
            path: '/test',
            name: 'test',
            component: require('../newPage/test.vue').default,
        },
        //
        {
            path: '/recruit',
            name: 'recruit',
            component: require('../newPage/recruit.vue').default,
            meta:{
                nav:'/recruit',
            }
        },
        //
        {
            path: '/consultation',
            name: 'consultation',
            component: require('../pages/BusinessArea/consultation.vue').default,
            meta:{
                nav:'/consultation',
            }
        }, //
        {
            path: '/assessment',
            name: 'assessment',
            component: require('../pages/BusinessArea/assessment.vue').default,
            meta:{
                nav:'/consultation',
            }
        }, //
        {
            path: '/Electronic',
            name: 'Electronic',
            component: require('../pages/BusinessArea/Electronic.vue').default,
            meta:{
                nav:'/consultation',
            }
        }, //HL7 FHIR
        {
            path: '/StandardTraining',
            name: 'StandardTraining',
            component: require('../pages/BusinessArea/StandardTraining.vue').default,
            meta:{
                nav:'/consultation',
            }
        }, //
        {
            path: '/conceptualDesign',
            name: 'conceptualDesign',
            component: require('../pages/BusinessArea/conceptualDesign.vue').default,
            meta:{
                nav:'/consultation',
            }
        }, //
        {
            path: '/projectManagement',
            name: 'projectManagement',
            component: require('../pages/BusinessArea/projectManagement.vue').default,
            meta:{
                nav:'/consultation',
            }
        }, //FHIR
        {
            path: '/guidance',
            name: 'guidance',
            component: require('../pages/BusinessArea/guidance.vue').default,
            meta:{
                nav:'/consultation',
            }
        },
        {
            path: '/Preparation',
            name: 'Preparation',
            component: require('../pages/BusinessArea/Preparation.vue').default,
            meta:{
                nav:'/consultation',
            }
        }, //
        {
            path: '/CooperationIndex',
            name: 'CooperationIndex',
            component: require('../pages/cooperationCase/CooperationIndex.vue').default,
            meta:{
                nav:'/CooperationIndex',
            }
        },//
        {
            path: '/IndustryIndex',
            name: 'IndustryIndex',
            component: require('../pages/industryDynamic/IndustryIndex.vue').default,
            meta:{
                nav:'/IndustryIndex',
            }
        },//
        {
            path: '/contactIndex',
            name: 'contactIndex',
            component: require('../pages/ContactUs/contactIndex.vue').default,
            meta:{
                nav:'/contactIndex',
            }
        }
    ]
})



