<template>
    <div style="background: gainsboro">
        <Nav></Nav>
        <img style="width:100%;" src="../../../static/img/other/banner4.png" alt="" />
        <div class="dynamic">
            <rightFixed></rightFixed>
            <div class="dynamicLeft">
                <div class="dynamicContent"
                     v-for="(item,index) in list"
                     :key="index"
                     @click="tab(index)"
                >
                    <p> {{item.title}}</p>
                    <p>
                        <span>:{{item.from}}</span>
                        <span>:{{item.date}}</span>
                        <span> :{{item.see}}</span>
                    </p>
                </div>
                <div class="navPage">
                    <el-pagination
                        background
                        layout="prev, pager, next"
                        :total="list.length">
                    </el-pagination>
                </div>
            </div>
            <div class="dynamicRight">
                <div v-for="(item,index) in list" :key="index" v-show="index==num" class="DRcontent">
                    <p>{{item.title}}</p>
                    <p class="DRsubtitle">
                        <span>:{{item.from}}</span>
                        <span>:{{item.date}}</span>
                        <span> :{{item.see}}</span>
                    </p>
                    <div class="MainContent">
                        <p><img :src="item.img" alt=""></p>
                        <p>{{item.content}}</p>
                        <p class="subtitle">{{item.subtitle}}</p>
                        <p class="subContent">{{item.subContent}}</p>
                    </div>
                </div>
            </div>
        </div>
        <publicFoot></publicFoot>
    </div>
</template>

<script>
  export default {
    name: "IndustryIndex",
    data() {
      return {
        num: '0',
        list: [
          {
            title: '""',
            from: '',
            date: '2020-04-04',
            see: '216',
            img: require('../../../static/review/image5.png'),
            content: '' +
              '' +
              '' +
              '\n' +
              '\n' +
              '',
            subtitle:'',
            subContent:'\n' +
              '\n' +
              '20194300060' +
              '6722430'
          }, {
            title: '',
            from: '',
            date: '2020-04-04',
            see: '216',
            img: require('../../../static/review/image5.png'),
            content: '' +
              '' +
              '' +
              '\n' +
              '\n' +
              '',
            subtitle:'',
            subContent:'\n' +
              '\n' +
              '20194300060' +
              '6722430'

          },
        ]
      }
    },
    methods: {
      tab(index) {
        this.num = index;
      }
    }
  }
</script>

<style scoped>
    .indutry {
        display: flex;
        flex-direction: row;
        width: 70%;
        margin: 1% 15%;
        flex-wrap: wrap;
    }

    .indutryChild {
        width: 30%;
        margin: 1%;
        box-shadow: 5px 5px 5px gainsboro;
    }

    .indutryContent {
        padding: 2% 0;
        border-bottom: 1px solid gainsboro;
    }

    .indutryContent p {
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap; /* */
        /*display: -webkit-box; !*  *!*/
        /*-webkit-line-clamp: 2; !**!*/
        /*-webkit-box-orient: vertical; !*  *!*/
    }

    .indutryContent p:nth-child(1) {
        padding: 1% 0;
        color: #282828;
        font-size: 18px;
        font-weight: bold;
    }

    .indutryContent p:nth-child(2) {
        padding: 2% 0;
        color: #a3a3a3;
        font-size: 14px;
        font-weight: bold;
    }

    .indutryTime {
        padding: 2% 0;
        text-align: center;
        font-size: 18px;
    }
</style>



