<template>
  <div class="consulation">
    <Nav></Nav>
    <!-- <img style="width:100%;" src="../../../static/img/banner6.jpg" alt=""> -->
    <banner :bannerList="bannerList" />
    <div class="content">
      <rightFixed></rightFixed>
      <contentNav></contentNav>
      <div class="contentDiv">
        <p  class="pageTitle" ></p>
        <img :src="content" alt="">
        <p class="contentDes">
          (IT)
          </p>
      </div>

    </div>
    <publicFoot></publicFoot>
  </div>
</template>

<script>
    import Banner from '@/components/banner';

    export default {
        name: "projectManagement",
        data() {
            return {
                bannerList: [{
                    name: require('../../../static/banner_bg/6.png'),
                    title: '',
                    type: 'text',
                    text: [
                        '',
                        '',
                        ''
                    ]
                }],
                content: require('../../../static/img/other/service.png')
            }
        },
        components: {
            Banner
        }
    }
</script>

<style scoped>
  .contentDiv {
    min-height: 70vh;
  }
  .contentTitle {
    background: white;
    border-bottom: 1px solid gainsboro;
    padding: 2% 4%;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
  }

  .contentDes {
    text-align: left;
    font-size: 14px;
    color: grey;
  }

  .contentDiv img {
    padding: 2% 0;
    width: 90%;
  }
</style>



